<template>
  <div class="hello">
    <el-row :gutter="20" class="functionalArea">
      <el-col :span="6">
        <el-button-group>
          <el-button type="primary" icon="el-icon-plus" @click="addBtnFn"></el-button>
          <el-button type="primary" icon="el-icon-share"></el-button>
          <el-button type="primary" icon="el-icon-delete" @click="clearBtnFn"></el-button>
        </el-button-group>
      </el-col>
      <el-col :span="6" :offset="6">
        <el-form>
          <el-form-item label="主题" :label-width="formLabelWidth">
            <el-select v-model="selectThemeId" @change="changeThemeFn" placeholder="请选择主题" style="width:100%;">
              <el-option v-for="(item, index) in themeList" :key="index" :label="item.label" :value="item.val"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="6">
        <el-input placeholder="请输入内容" v-model="searchTheme" clearable>
          <template slot="append">
            <el-button type="success" plain>搜索</el-button>
          </template>
        </el-input>
      </el-col>
    </el-row>

    <el-table
            ref="multipleTable"
            :data="tableData"
            style="width: 100%"
            :header-cell-style="headerCellStyle"
            :cell-style="cellStyle"
            @select="handleSelectionChange"
            @select-all="selectAllFn"
    >
      <el-table-column
              type="selection"
              width="55">
      </el-table-column>
      <el-table-column
              label="序号"
              width="60">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.index }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="事项"
              width="240">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="流程"
              width="280">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.process }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="时限（天）"
              width="180">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>主题: {{ scope.row.subject }}</p>
            <p>步骤数: {{ scope.row.step }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{ scope.row.timeout }}</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column
              label="操作"
              min-width="240">
        <template slot-scope="scope">
          <el-button
                  size="mini"
                  @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="添加事项" :visible.sync="addFormVisible" width="800px">
      <el-form :model="form">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="主题" :label-width="formLabelWidth">
              <el-input v-model="form.subject" autocomplete="off" disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="事项" :label-width="formLabelWidth">
              <el-input v-model="form.name" placeholder="请输入事项" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="行使层级" :label-width="formLabelWidth">
              <el-select v-model="form.level" placeholder="请选择行使层级" style="width:100%;">
                <el-option v-for="(item, index) in levelList" :key="index" :label="item.label" :value="item.val"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="流程" :label-width="formLabelWidth">
              <el-input v-model="form.process" placeholder="格式：1-2-3" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="时限" :label-width="formLabelWidth">
              <el-input v-model="form.timeout" placeholder="请输入时限(天)" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="类型" :label-width="formLabelWidth">
              <el-select v-model="form.cate" placeholder="请选择类型" style="width:100%;">
                <el-option v-for="(item, index) in styleList" :key="index" :label="item.label" :value="item.val"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="常办" :label-width="formLabelWidth">
              <el-select v-model="form.frequent" placeholder="请选择服务区" style="width:100%;">
                <el-option v-for="(item, index) in frequentList" :key="index" :label="item.label" :value="item.val"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addSureFn">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="编辑事项" :visible.sync="fixFormVisible" width="800px">
      <el-form :model="form1">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="主题" :label-width="formLabelWidth">
              <el-input v-model="form1.subject" autocomplete="off" disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="事项" :label-width="formLabelWidth">
              <el-input v-model="form1.name" placeholder="请输入事项" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="行使层级" :label-width="formLabelWidth">
              <el-select v-model="form1.level" placeholder="请选择行使层级" style="width:100%;">
                <el-option v-for="(item, index) in levelList" :key="index" :label="item.label" :value="item.val"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="流程" :label-width="formLabelWidth">
              <el-input v-model="form1.process" placeholder="格式：1-2-3" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="时限" :label-width="formLabelWidth">
              <el-input v-model="form1.timeout" placeholder="请输入时限(天)" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="类型" :label-width="formLabelWidth">
              <el-select v-model="form1.cate" placeholder="请选择类型" style="width:100%;">
                <el-option v-for="(item, index) in styleList" :key="index" :label="item.label" :value="item.val"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="常办" :label-width="formLabelWidth">
              <el-select v-model="form1.frequent" placeholder="请选择服务区" style="width:100%;">
                <el-option v-for="(item, index) in frequentList" :key="index" :label="item.label" :value="item.val"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="fixFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="fixSureFn">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import { baseUrl } from '@/api/unsplash/baseUrl'
export default {
  name: 'ATheme',
  data () {
    return {
      selectThemeId: '',
      selectThemeName: '',

      searchTheme: '',
      tableData: [],
      baseUrl: baseUrl,
      themeList: [],
      levelList: [
        {
          label: '省,市（州）, 县（市、区）',
          val: '省,市（州）, 县（市、区）'
        },
        {
          label: '市（州）, 县（市、区）',
          val: '市（州）, 县（市、区）'
        },
        {
          label: '县（市、区）',
          val: '县（市、区）'
        }
      ],
      styleList: [
        {
          label: '行政许可',
          val: '行政许可'
        }
      ],
      frequentList: [
        {
          label: '是',
          val: '是'
        },
        {
          label: '否',
          val: '否'
        }
      ],

      addFormVisible: false,
      formLabelWidth: '120px',
      form: {
        subject: '',
        name: '',
        level: '县（市、区）',
        process: '',
        timeout: '',
        cate: '行政许可',
        frequent: '是'
      },
      fixFormVisible: false,
      form1: {
        subject: '',
        name: '',
        level: '县（市、区）',
        process: '',
        timeout: '',
        cate: '行政许可',
        frequent: '是',
        matterId: ''
      },

      selectIds: []
    }
  },
  watch: {
  },
  created: function () {
    this.getThemeFn()
  },
  methods: {
    changeThemeFn () {
      for (let i = 0; i < this.themeList.length; i++) {
        if (this.themeList[i].val == this.selectThemeId) {
          this.selectThemeName = this.themeList[i].label
          this.getMatterFn(this.selectThemeName)
          break
        }
      }
    },
    addBtnFn () {
      if (this.selectThemeName) {
        this.addFormVisible = true
        this.form.subject = this.selectThemeName
      } else {
        this.$message({
          type: 'warning',
          message: '未选择主题!'
        })
      }
    },
    addSureFn () {
      let temp = {
        subject_id: this.selectThemeId,
        name: this.form.name.replace(/(^\s*)|(\s*$)/g, ''),
        process: this.form.process.replace(/(^\s*)|(\s*$)/g, ''),
        step: this.form.process.replace(/(^\s*)|(\s*$)/g, '').split('-').length,
        level: this.form.level,
        cate: this.form.cate,
        frequent: this.form.frequent,
        timeout: this.form.timeout,
        industry: '1', // 无意义
        user_id: sessionStorage.user_id
      }
      if (temp.name != '' && temp.process != '' && temp.timeout != '') {
        this.addFn(temp)
      } else {
        this.$message({
          type: 'warning',
          message: '请补全信息!'
        })
      }
    },
    handleEdit (index, row) {
      console.log(index, row)
      this.fixFormVisible = true
      this.form1.subject = this.selectThemeName
      this.form1.name = row.name

      if (row.level) {
        this.form1.level = row.level
      } else {
        this.form1.level = '县（市、区）'
      }
      this.form1.process = row.process
      this.form1.timeout = row.timeout
      if (row.cate) {
        this.form1.cate = row.cate
      } else {
        this.form1.cate = '行政许可'
      }
      if (row.frequent) {
        this.form1.frequent = row.frequent
      } else {
        this.form1.frequent = '是'
      }
      this.form1.matterId = row.id
    },
    fixSureFn () {
      let temp = {
        id: this.form1.matterId,
        subject_id: this.selectThemeId,
        name: this.form1.name.replace(/(^\s*)|(\s*$)/g, ''),
        process: this.form1.process.replace(/(^\s*)|(\s*$)/g, ''),
        step: this.form1.process.replace(/(^\s*)|(\s*$)/g, '').split('-').length,
        level: this.form1.level,
        cate: this.form1.cate,
        frequent: this.form1.frequent,
        timeout: this.form1.timeout,
        industry: '1', // 无意义
        user_id: sessionStorage.user_id
      }
      console.log(temp)
      if (temp.name != '' && temp.process != '' && temp.timeout != '') {
        this.fixFn(temp)
      } else {
        this.$message({
          type: 'warning',
          message: '请补全信息!'
        })
      }
    },
    clearBtnFn () {
      if (this.selectIds.length) {
        this.$confirm('确定删除所选事项', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let params = {
            id_list: this.selectIds,
            user_id: sessionStorage.user_id
          }
          this.deleteSingleFn(params)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      } else {
        this.$message({
          type: 'warning',
          message: '未选择事项!'
        })
      }
    },
    handleDelete (index, row) {
      console.log(row)
      this.$confirm('确定删除事项：' + row.name, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let params = {
          id_list: [row.id],
          user_id: sessionStorage.user_id
        }
        this.deleteSingleFn(params)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    async getThemeFn () {
      try {
        let params = {}
        let res = await this.$api.unsplash.getSubjects(params)
        console.log(res)
        this.themeList = []
        if (res.errno == '0') {
          for (let i = 0; i < res.data.subjects.length; i++) {
            this.themeList.push({
              label: res.data.subjects[i].name,
              val: res.data.subjects[i].id
            })
          }
          this.selectThemeId = this.themeList[0].val
          this.selectThemeName = this.themeList[0].label
          this.getMatterFn(this.selectThemeName)
        } else {
          this.$alert('服务器错误', '警告', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async getMatterFn (theme) {
      try {
        let params = {
          subject: theme
        }
        let res = await this.$api.unsplash.getMatters(params)
        console.log(res)
        if (res.errno == '0') {
          this.tableData = res.data.items
          this.tableData = this.addSequence(this.tableData)
        } else {
          this.$alert('服务器错误', '警告', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async addFn (params) {
      try {
        let res = await this.$api.unsplash.postAddMatter(params)
        console.log(res)
        if (res.errno == '0') {
          this.addFormVisible = false
          this.$message({
            type: 'success',
            message: '增加事项成功'
          })
          this.getMatterFn(this.selectThemeName)
        } else {
          this.$message({
            type: 'warning',
            message: '增加事项失败'
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async fixFn (params) {
      try {
        let res = await this.$api.unsplash.postfixMatter(params)
        if (res.errno == '0') {
          this.fixFormVisible = false
          this.$message({
            type: 'success',
            message: '修改事项成功'
          })
          this.getMatterFn(this.selectThemeName)
        } else {
          this.$message({
            type: 'warning',
            message: '修改事项失败'
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async deleteSingleFn (params) {
      try {
        let res = await this.$api.unsplash.postClearMatter(params)
        console.log(res)
        if (res.errno == '0') {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.selectIds = []
          this.getMatterFn(this.selectThemeName)
        } else {
          this.myAlert('服务器错误', '警告', this)
        }
      } catch (e) {
        console.log(e)
      }
    },
    handleSelectionChange (selection, row) { // 单击
      console.log(selection)
      this.selectIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectIds.push(selection[i].id)
      }
    },
    selectAllFn (selection) { // 全选
      console.log(selection)
      this.selectIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectIds.push(selection[i].id)
      }
    },
    headerCellStyle ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex > -1) {
        return {
          'text-align': 'center'
        }
      }
    },
    cellStyle ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex >= 0) {
        return {
          'text-align': 'center'
        }
      }
    },
    myAlert (txt, title, self) {
      self.$alert(txt, title, {
        confirmButtonText: '确定',
        callback: action => {
        }
      })
    },
    addSequence (temp) {
      for (let i = 0; i < temp.length; i++) {
        temp[i].index = i + 1
      }
      return temp
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .functionalArea{
    height: 80px;
  }
  .headerCellStyle{
    text-align: center;
  }

</style>



